const initCatalogSidebar = () => {
  const sidebar = document.querySelector('.sidebar');

  if (!sidebar) {
    return;
  }

  if (sidebar) {
    const sidebarList = sidebar.querySelectorAll('.sidebar__list');
    const buttonSidebar = sidebar.querySelector('.sidebar__btn');

    if (buttonSidebar) {
      buttonSidebar.addEventListener('click', () => {
        buttonSidebar.classList.toggle('sidebar__btn--active');
      });
    }
    sidebarList.forEach((item) => {
      item.addEventListener('click', () => {
        item.classList.toggle('sidebar__list--active');
      });

      item.addEventListener('keydown', (evt) => {
        if (evt.key === ' ') {
          evt.preventDefault();
          item.classList.toggle('sidebar__list--active');
        }
      });
    });
  }
};

export {initCatalogSidebar};
