const initActiveIcons = () => {
  const filters = document.querySelector('.form-filters');
  // const breakpoint = window.matchMedia('(min-width:1920px)');

  if (!filters) {
    return;
  }

  if (filters) {
    const itemBrandsList = filters.querySelectorAll('.brands-list .brands-item input');
    const itemBrandsSublist = filters.querySelectorAll('.brands-sublist .brands-item input');
    const itemCheckbox = filters.querySelectorAll('.checkbox-item input');
    const itemActive = filters.querySelectorAll('.active-icon');
    const rangeList = filters.querySelectorAll('.js-range');
    const activeTitle = filters.querySelectorAll('.active-icon__title--range');

    rangeList.forEach((range) => {
      const rangeSlider = range.querySelector('.js-range-slider');
      const inputMin = range.querySelector('.js-range-input-min input');
      const inputMax = range.querySelector('.js-range-input-max input');

      const includesIcon = (item1, item2) => {
        activeTitle.forEach((element) => {
          const parent = element.parentNode;

          if (range.id === parent.dataset.parent) {
            element.innerHTML = `${item1} &#8212 ${item2}`;
            parent.classList.add('active-icon--active');

            // if (breakpoint.matches) {
            //   filterItems.forEach((filter) => {
            //     if (parent.dataset.parent === filter.dataset.parent) {
            //       filter.classList.add('form-filters__item--active');
            //     }
            //   });
            // }
          }
        });
      };

      rangeSlider.noUiSlider.on('change', (handle) => {
        includesIcon(handle[0], handle[1]);
      });

      inputMin.addEventListener('change', () => {
        includesIcon(inputMin.value, inputMax.value);
      });

      inputMax.addEventListener('change', () => {
        includesIcon(inputMin.value, inputMax.value);
      });
    });

    const displaysIcons = (items, elements, items2) => {
      items.forEach((item) => {
        item.addEventListener('input', () => {
          if (items2) {
            items2.forEach((e) => {
              if (item.checked && item.value === e.value) {
                e.checked = true;
              }
            });
          }
          elements.forEach((element) => {
            if (item.checked && item.value === element.dataset.parent) {
              element.classList.add('active-icon--active');
            } else if (!item.checked && item.value === element.dataset.parent) {
              element.classList.remove('active-icon--active');
            }
          });
        });
      });
    };

    const removesCheckboxes = (items1, items2) => {
      items1.forEach((i) => {
        items2.forEach((e) => {
          e.addEventListener('input', () => {
            if (!e.checked && e.value === i.value) {
              i.checked = false;
            }
          });
        });
      });
    };

    const removesIcons = (elements, items) => {
      elements.forEach((element) => {
        element.addEventListener('click', () => {
          items.forEach((item) => {
            if (item.value === element.dataset.parent || item.id === element.dataset.parent) {
              item.checked = false;
              element.classList.remove('active-icon--active');
            }
          });
        });
      });
    };

    displaysIcons(itemBrandsList, itemActive, itemBrandsSublist);
    displaysIcons(itemBrandsSublist, itemActive, itemBrandsList);
    removesCheckboxes(itemBrandsSublist, itemBrandsList);
    removesCheckboxes(itemBrandsList, itemBrandsSublist);
    displaysIcons(itemCheckbox, itemActive);
    removesIcons(itemActive, itemBrandsList);
    removesIcons(itemActive, itemBrandsSublist);
    removesIcons(itemActive, itemCheckbox);
    removesIcons(itemActive, rangeList);
  }
};

export {initActiveIcons};
