import {getHeaderHeight} from './header';
import {initScrollAnimation, changeAnimation, startAnimations, stopAnimations} from './scroll-animation';

const MEDIA_DESKTOP = '(min-width: 1024px)';

const mainWrapper = document.querySelector('.scroll-sequence');

const updateHeights = (needReset) => {
  const scrollSlide = document.querySelector('.scroll-sequence__slide');
  const scrollSlideHeight = scrollSlide.offsetHeight;
  const scrollSeqBottom = document.querySelector('.scroll-sequence__bottom');
  const scrollSeqBottomContent = document.querySelector('.scroll-sequence__bottom-content');
  const bottomContentHeight = scrollSeqBottomContent.offsetHeight;
  scrollSeqBottom.style.height = !needReset
    ? `${bottomContentHeight + scrollSlideHeight + getHeaderHeight()}px`
    : '';

  if (getHeaderHeight()) {
    scrollSeqBottomContent.style.paddingTop = !needReset
      ? `${getHeaderHeight()}px`
      : '';
  }
};

const checkMedia = () => {
  if (window.matchMedia(MEDIA_DESKTOP).matches) {
    updateHeights();
    startAnimations();
  } else {
    updateHeights(true);
    stopAnimations();
  }
};

let currentScrollY = window.scrollY;

const onWindowScroll = () => {
  const scrollDiff = window.scrollY - currentScrollY;
  if (window.matchMedia(MEDIA_DESKTOP).matches) {
    changeAnimation(scrollDiff);
  }
  currentScrollY = window.scrollY;
};

const initScrollSequence = () => {
  if (!mainWrapper) {
    return;
  }

  initScrollAnimation(mainWrapper);

  checkMedia();
  window.addEventListener('resize', () => {
    setTimeout(checkMedia, 200);
  });

  window.addEventListener('scroll', onWindowScroll);
};

export {initScrollSequence, getHeaderHeight};
