const initSticky = () => {
  const stickyFilter = document.querySelector('.filters');

  if (!stickyFilter) {
    return;
  }

  if (stickyFilter) {
    const observer = new IntersectionObserver(
        ([item]) => item.target.classList.toggle('filters--active', item.intersectionRatio < 1),
        {
          rootMargin: '-15px 0px 0px 0px',
          threshold: [1],
        }
    );

    observer.observe(stickyFilter);
  }
};

export {initSticky};
