const initFilterBar = () => {
  const filters = document.querySelector('.filters');

  if (!filters) {
    return;
  }

  if (filters) {
    const overlay = document.querySelector('.overlay');
    const filtersList = filters.querySelector('.filters__wrapper');
    const buttonFilters = filters.querySelector('.filters__toggle');

    if (buttonFilters) {
      buttonFilters.addEventListener('click', () => {
        buttonFilters.classList.toggle('filters__toggle--active');
        filtersList.classList.toggle('filters__wrapper--active');

        if (!buttonFilters.classList.contains('filters__toggle--active')) {
          overlay.classList.remove('overlay--active');
        }
      });
    }
    // filtersList.forEach((item) => {
    //   item.addEventListener('click', () => {
    //     item.classList.toggle('filters__block-wrapper--active');
    //   });

    //   item.addEventListener('keydown', (evt) => {
    //     if (evt.key === ' ') {
    //       evt.preventDefault();
    //       item.classList.toggle('filters__block-wrapper--active');
    //     }
    //   });
    // });
  }
};


export {initFilterBar};
