import {getHeaderHeight} from './header';

let mainWrapper = null;
let intersectionElements;
let animationObserver;

const Section = {};

const RANGE = {
  BG_OPACITY: {
    MIN: 0,
    MAX: 1,
  },
  LINE_SCALE: {
    MIN: 0,
    MAX: 1,
  },
  TRANSLATE: {
    MIN: 0,
    MAX: 100,
  },
};

const animElements = [];

const animationMain = (sectionElement, animName, viewportHeight) => {
  const {element, firstLoad} = sectionElement;
  const opElements = element.querySelectorAll('.js-opacity');
  const transElements = element.querySelectorAll('.js-translate');
  if (firstLoad) {
    animElements.push(...opElements, ...transElements);
    Section[animName].firstLoad = false;
  }
  const clientRect = element.getBoundingClientRect();
  const oneOpacity = (RANGE.BG_OPACITY.MAX - RANGE.BG_OPACITY.MIN) / viewportHeight;
  const oneTranslate = (RANGE.TRANSLATE.MAX - RANGE.TRANSLATE.MIN) / viewportHeight;
  let bgOpacity = 0;
  let elTranslate = 0;

  if (clientRect.top >= viewportHeight) {
    bgOpacity = RANGE.BG_OPACITY.MIN;
    elTranslate = RANGE.TRANSLATE.MAX;
  } else if (clientRect.bottom <= viewportHeight) {
    bgOpacity = RANGE.BG_OPACITY.MAX;
    elTranslate = RANGE.TRANSLATE.MIN;
  } else {
    bgOpacity = oneOpacity * (viewportHeight - (clientRect.bottom - viewportHeight));
    elTranslate = oneTranslate * (clientRect.bottom - viewportHeight);
  }

  if (opElements.length > 0) {
    opElements.forEach((el) => {
      el.style.opacity = bgOpacity;
    });
  }

  if (transElements.length > 0) {
    transElements.forEach((el) => {
      if (el.classList.contains('js-left')) {
        el.style.transform = `translateX(-${elTranslate}%)`;
      }
      if (el.classList.contains('js-right')) {
        el.style.transform = `translateX(${elTranslate}%)`;
      }
    });
  }
};

const animationGreeting = (sectionElement, animName, viewportHeight) => {
  const {element, firstLoad} = sectionElement;
  const bg = element.querySelector('.js-bg');
  const line = element.querySelector('.js-line');
  if (firstLoad) {
    animElements.push(bg, line);
    Section[animName].firstLoad = false;
  }
  const clientRect = element.getBoundingClientRect();
  const oneOpacity = (RANGE.BG_OPACITY.MAX - RANGE.BG_OPACITY.MIN) / viewportHeight;
  const oneScale = (RANGE.LINE_SCALE.MAX - RANGE.LINE_SCALE.MIN) / viewportHeight;
  let bgOpacity = 0;
  let lineScale = 0;

  if (clientRect.bottom <= getHeaderHeight()) {
    bgOpacity = RANGE.BG_OPACITY.MIN;
  } else if (clientRect.bottom >= viewportHeight) {
    bgOpacity = RANGE.BG_OPACITY.MAX;
  } else {
    bgOpacity = clientRect.bottom * oneOpacity;
  }

  if (clientRect.top >= viewportHeight) {
    lineScale = RANGE.LINE_SCALE.MIN;
  } else if (clientRect.bottom <= viewportHeight) {
    lineScale = RANGE.LINE_SCALE.MAX;
  } else {
    lineScale = oneScale * (viewportHeight - (clientRect.bottom - viewportHeight));
  }

  if (bg) {
    bg.style.opacity = bgOpacity;
  }
  if (line) {
    line.style.transform = `scaleX(${lineScale})`;
  }
};

const animationPromo = () => {
  // eslint-disable-next-line no-console
  // console.log('WIP');
};

const changeAnimation = () => {
  const viewportHeight = document.documentElement.clientHeight;

  for (let [animName, option] of Object.entries(Section)) {
    if (option.isActive) {
      switch (animName) {
        case 'promo':
          animationPromo();
          break;
        case 'greeting':
          animationGreeting(Section[animName], animName, viewportHeight);
          break;
        case 'main':
        case 'geography':
          animationMain(Section[animName], animName, viewportHeight);
          break;
      }
    }
  }
};

const handlerIntersection = (entries) => {
  entries.forEach((entry) => {
    const target = entry.target;
    const elementName = target.dataset.scrollContainer;

    if (entry.isIntersecting) {
      Section[elementName].isActive = true;
    } else {
      Section[elementName].isActive = false;
    }
  });
};

let isStopped = true;

const stopAnimations = () => {
  if (isStopped) {
    return;
  }
  intersectionElements.forEach((slide) => {
    animationObserver.unobserve(slide);
  });
  animElements.forEach((el) => {
    el.style = '';
  });
  isStopped = true;
};

const startAnimations = () => {
  if (!isStopped) {
    return;
  }

  intersectionElements.forEach((slide) => {
    animationObserver.observe(slide);
  });
  changeAnimation();
  isStopped = false;
};

const setElement = (slide) => {
  const sectionName = slide.dataset.scrollContainer;
  if (!sectionName) {
    return;
  }
  Section[sectionName] = {
    isActive: true,
    element: slide,
    firstLoad: true,
  };
};

const initScrollAnimation = (wrapper) => {
  if (!wrapper) {
    return;
  }
  mainWrapper = wrapper;

  animationObserver = new IntersectionObserver(handlerIntersection);
  intersectionElements = mainWrapper.querySelectorAll('[data-scroll-container]');
  intersectionElements.forEach(setElement);
};

export {initScrollAnimation, changeAnimation, startAnimations, stopAnimations};
