// –ö–æ–Ω—Å—Ç–∞–Ω—Ç—ã

const dropdownToggle = document.querySelectorAll('[data-dropdown-toggle]');
const dropdownList = document.querySelectorAll('[data-dropdown]');
const DROPDOWN_TOGGLE_CLASS = '.main-nav__dropdown-toggle';
const DROPDOWN_LIST_CLASS = '.main-nav__dropdown';

// –ó–∞–∫—Ä—ã—Ç–∏–µ –º–µ–Ω—é –ø–æ –∫–ª–∏–∫—É –ø–æ –æ–≤–µ—Ä–ª–µ—é

const navOverlayClick = (evt) => {
  if (!evt.target.closest(DROPDOWN_TOGGLE_CLASS) && !evt.target.closest(DROPDOWN_LIST_CLASS)) {
    dropdownList.forEach((item) => item.setAttribute('hidden', 'hidden'));
    dropdownToggle.forEach((item) => item.querySelector('svg').classList.remove('is-open'));
  }
};

// –§—É–Ω–∫—Ü–∏—è –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏—è —Ä–∞–∑–¥–µ–ª–æ–≤ –º–µ–Ω—é

const onNavToggleClick = (evt, toggleName) => {
  // let buttonElement;
  let buttonId;
  let toggleIcon;
  let listElement;
  if (evt.target === dropdownToggle || evt.target.closest(toggleName)) {
    // buttonElement = evt.target.closest(toggleName);
    buttonId = evt.target.closest(toggleName).dataset.dropdownToggle;
    toggleIcon = evt.target.closest(toggleName).querySelector('svg');
  }
  if (!buttonId) {
    return;
  }
  dropdownList.forEach((list) => {
    if (list.dataset.dropdown === buttonId) {
      listElement = list;
    }
  });
  if (listElement.hasAttribute('hidden')) {
    dropdownList.forEach((list) => list.setAttribute('hidden', 'hidden'));
    dropdownToggle.forEach((toggle) => toggle.querySelector('svg').classList.remove('is-open'));
    listElement.removeAttribute('hidden');
    toggleIcon.classList.add('is-open');
    document.addEventListener('click', navOverlayClick);
  } else {
    listElement.setAttribute('hidden', 'hidden');
    toggleIcon.classList.remove('is-open');
    document.removeEventListener('click', navOverlayClick);
  }
};

// –£—Å—Ç–∞–Ω–æ–≤–∫–∞ –æ–±—Ä–∞–±–æ—Ç—á–∏–∫–æ–≤ –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏—è —Ä–∞–∑–¥–µ–ª–æ–≤ –º–µ–Ω—é.

const initNavToggle = () => {
  dropdownToggle.forEach((toggle) => {
    toggle.addEventListener('click', (evt) => {
      onNavToggleClick(evt, DROPDOWN_TOGGLE_CLASS);
    });
  });
};

export {initNavToggle};
