/* globals Swiper */
function initGeographySlider() {
  const sliderGeography = document.querySelector('.project-geography__slider');

  if (!sliderGeography) {
    return;
  }

  const breakpoint = window.matchMedia('(min-width:1024px)');
  let slider;

  const initSliderCard = () => {
    // eslint-disable-next-line
    slider = new Swiper(sliderGeography, {
      a11y: {
        enabled: true,
        firstSlideMessage: '–ü–µ—Ä–≤—ã–π —Å–ª–∞–π–¥',
        lastSlideMessage: '–ü–æ—Å–ª–µ–¥–Ω–∏–π —Å–ª–∞–π–¥',
        nextSlideMessage: '–°–ª–µ–¥—É—é—â–∏–π —Å–ª–∞–π–¥',
        prevSlideMessage: '–ü—Ä–µ–¥—ã–¥—É—â–∏–π —Å–ª–∞–π–¥',
        paginationBulletMessage: '–ü–µ—Ä–µ–π—Ç–∏ –∫ —Å–ª–∞–π–¥—É {{index}}',
        slideLabelMessage: '–°–ª–∞–π–¥ {{index}} –∏–∑ {{slidesLength}}',
      },
      wrapperClass: 'project-geography__cards',
      slideClass: 'card',
      slideActiveClass: 'card--active',
      slidePrevClass: 'card--prev',
      slideNextClass: 'card--next',
      loop: true,
      pagination: {
        el: '.project-geography__pagination',
        clickable: true,
        type: 'bullets',
        bulletElement: 'button',
        bulletClass: 'pagination__button pagination__button--lg pagination__button--rectangle',
        bulletActiveClass: 'pagination__button--active',
        renderBullet(index, className) {
          return `<button class="${className}" type="button" data-slide="${++index}"></button>`;
        },
      },
      keyboard: {
        enabled: true,
      },
      breakpoints: {
        320: {
          slidesPerView: 'auto',

          slidesPerGroup: 1,
        },

      },
    });
  };

  const breakpointChecker = () => {
    if (breakpoint.matches) {
      if (sliderGeography.classList.contains('swiper-initialized')) {
        slider.destroy(false, true);
      }
    } else {
      initSliderCard();
    }
  };
  breakpoint.addListener(breakpointChecker);
  breakpointChecker();
  // return slider;
}

export {initGeographySlider};


