const initCalcRent = () => {
  const requestBlock = document.querySelectorAll('.request-block');
  const buttonReservation = document.querySelectorAll('.btn-reservation');

  if (!buttonReservation) {
    return;
  }

  if (buttonReservation) {
    buttonReservation.forEach((item) => {
      item.addEventListener('click', () => {
        if (item.classList.contains('btn-reservation--active')) {
          item.classList.remove('btn-reservation--active');
          item.innerHTML = '–ê—Ä–µ–Ω–¥–æ–≤–∞—Ç—å';
        } else {
          item.classList.add('btn-reservation--active');
          item.innerHTML = '–û—Ñ–æ—Ä–º–∏—Ç—å –∑–∞—è–≤–∫—É';
        }
      });
    });
  }

  if (!requestBlock) {
    return;
  }

  if (requestBlock) {
    requestBlock.forEach((element) => {
      const inputRequest = element.querySelector('input[name="request"]');
      const inputFormula = element.querySelector('input[name="formula"]');
      const requestButton = element.querySelectorAll('.request-block__button');

      const calculates–°ost = () => {
        if (inputRequest.value >= 3 && inputRequest.value <= 10) {
          inputFormula.value = `1 500 √ó ${inputRequest.value} = ${1500 * inputRequest.value} —Ä—É–±.`;
        }
        if (inputRequest.value >= 11 && inputRequest.value <= 20) {
          inputFormula.value = `1 200 √ó ${inputRequest.value} = ${1200 * inputRequest.value} —Ä—É–±.`;
        }
        if (inputRequest.value >= 21) {
          inputFormula.value = `1 000 √ó ${inputRequest.value} = ${1000 * inputRequest.value} —Ä—É–±.`;
        }
      };

      calculates–°ost();

      requestButton.forEach((item) => {
        item.addEventListener('click', () => {
          if (item.classList.contains('request-block__button--minus')) {
            item.nextElementSibling.stepDown();
          } else if (item.classList.contains('request-block__button--plus')) {
            item.previousElementSibling.stepUp();
          }
          calculates–°ost();
        });
      });
    });
  }
};

export {initCalcRent};
